import React  from 'react';
import styled from 'styled-components';

const Body = styled.div`
    width: 100%;
    height: 100%;
    overflow-x: hidden;
    overflow-y: auto;
    
    ::-webkit-scrollbar {
        width: 6px;
    }
    ::-webkit-scrollbar-track {
        border-radius: 10px;
    }
    ::-webkit-scrollbar-thumb {
        background-color: rgba(50, 50, 50, .5);
        border-radius: 10px;
    }
`;

const Text = styled.p`
    font-family: 'Lato', sans-serif;
    font-weight: 400;
    font-size: 16px;
    user-select: none;
    letter-spacing: 1px;
    color: grey;
`;

const Front = (props) => (
    <Body>
    <Text>
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        
        </Text>
    </Body>
);

export default Front;



